'use client';
import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { getAllPosts, sendConnectionRequest } from '@/config/redux/action/postAction';
import Style from './viewProfile.module.css';
import { BASE_URL } from '@/config';

export default function ViewProfileClient({ userProfile, username }) {
  const dispatch = useDispatch();
  const postReducer = useSelector((state) => state.postReducer);
  const authState = useSelector((state) => state.auth);

  const [userPosts, setUserPosts] = useState([]);
  const [isCurrentUserInConnection, setIsCurrentUserInConnection] = useState(false);
  const [isConnectionNull, setIsConnectionNull] = useState(true);

  // Fetch posts
  useEffect(() => {
    dispatch(getAllPosts());
  }, [dispatch]);

  // Filter posts of the current user
  useEffect(() => {
    const posts = postReducer.posts.filter((post) => post.userId.username === username);
    setUserPosts(posts);
  }, [postReducer.posts, username]);

  // Check connection status
  useEffect(() => {
    if (authState.connections?.some(user => user.connectionId._id === userProfile.userId._id)) {
      setIsCurrentUserInConnection(true);
      if (authState.connections.find(user => user.connectionId._id === userProfile.userId._id).status_accepted === "true") {
        setIsConnectionNull(false);
      }
    }
  }, [authState.connections, userProfile.userId._id]);

  return (
    <div className={Style.container}>
      <div className={Style.backDropContainer}>
        <img
          className={Style.backDrop}
          src={
            userProfile?.userId?.profilePicture
              ? userProfile.userId.profilePicture.startsWith("/")
                ? userProfile.userId.profilePicture
                : `${BASE_URL}/upload/${userProfile.userId.profilePicture}`
              : "/images/default.jpg"
          }
          alt="backdrop"
        />
      </div>

      <div className={Style.profileContainer__details}>
        <div style={{ display: "flex", gap: "0.7rem" }}>
          <div style={{ flex: "0.8" }}>
            <div style={{ display: "flex", width: "fit-content", alignItems: "center", gap: "1.2rem" }}>
              <h2>{userProfile?.userId?.name}</h2>
              <p style={{ color: "grey" }}>{userProfile?.userId?.username}</p>
            </div>

            {userProfile && !isCurrentUserInConnection ? (
              <button
                className={Style.connectBtn}
                onClick={() => {
                  dispatch(sendConnectionRequest({
                    token: localStorage.getItem("token"),
                    connectionId: userProfile.userId._id
                  }));
                  setIsConnectionNull(false);
                }}
              >
                {isConnectionNull ? "Connect" : "Pending"}
              </button>
            ) : (
              <button className={Style.connectedButton}>Connected</button>
            )}

            <div>
              <p>{userProfile?.bio}</p>
            </div>

            <div style={{ flex: "0.2" }}>
              <h3>Recent Activity</h3>
              {userPosts.map((posts) => (
                <div key={posts._id} className={Style.postCard}>
                  <div className={Style.card__profileContainer}>
                    {posts.media !== "" ? (
                      <img src={`${BASE_URL}/${posts.media}`} alt="" />
                    ) : (
                      <div style={{ width: "3.4rem", height: "3.4rem" }} />
                    )}
                  </div>
                </div>
              ))}
            </div>
          </div>

          <div style={{ flex: "0.2" }}></div>
        </div>
      </div>
    </div>
  );
}
